@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400&display=swap);
.y-center{display:flex;flex-direction:column;justify-content:center}html{font-size:10px}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}*{box-sizing:border-box;outline:none;margin:0;padding:0;font-family:'Montserrat', sans-serif}a{text-decoration:none}p{margin:0;padding:0}h1,h2,h3,h4,h5{color:#fff;line-height:2}ul{margin:10px 0;padding-left:15px}ul li{font-size:16px;line-height:1.75}h1{font-size:3.2rem}h2{font-size:2.6rem}h3{font-size:2.2rem}h4{font-weight:600;font-size:1.8rem}.App{display:flex;flex-direction:column;min-height:100vh;background-color:#1b2838;color:#acb2b8}.main{padding-top:50px;position:relative;min-height:100%;z-index:2;flex:1 1 100%}.container{display:flex;flex-direction:column;height:100%;max-width:900px;margin:0 auto;padding:10px}.game-info{padding:20px 0;display:flex;grid-gap:30px 30px;gap:30px 30px}.game-info .left-info{display:flex;flex-direction:column;grid-gap:10px;gap:10px;flex:0 0 65%}.game-info .left-info:hover .header-img-game{-webkit-transform:scale(1.03);transform:scale(1.03);box-shadow:0 3px 8px rgba(0,0,0,0.35)}.game-info .right-info{margin-top:55px;display:flex;flex-direction:column;grid-gap:5px;gap:5px}.game-info .price{font-size:1.8rem;font-weight:500;color:#eee}.bg-game{position:absolute;width:100%;height:100vh;top:0;left:0;z-index:-1;object-fit:cover}.header-img-game{width:100%;transition:all 0.2s ease-in-out}.button-wrap{position:relative;display:flex;align-items:center;grid-gap:20px;gap:20px}.loader{position:relative;height:40px;opacity:0;-webkit-transform:scale(1.2);transform:scale(1.2);transition:all 0.2s ease-in-out}.loader.active{display:inline-block;opacity:1;transition:all 0.2s ease-in-out}.btn{display:inline-block;font-size:20px;font-weight:500;line-height:2;padding:0 20px;border-radius:2px;border:none;cursor:pointer;text-decoration:none;color:#d2efa9;text-shadow:1px 1px 0px rgba(0,0,0,0.3);background:#6fa720;background:linear-gradient(to right, #75b022 5%, #588a1b 95%)}.btn:hover{color:#fff;background:#8ed629;background:linear-gradient(to bottom, #8ed629 5%, #6aa621 95%);background:linear-gradient(to right, #8ed629 5%, #6aa621 95%)}.btn:disabled{color:#bfcfa9;background:#5c6d45;background:linear-gradient(to right, #849768 5%, #768b56 95%)}.btn:disabled:hover{color:#bfcfa9;background:#5c6d45;background:linear-gradient(to right, #849768 5%, #768b56 95%)}@media (max-width: 850px){.game-info{flex-direction:column}.game-info .right-info{margin-top:5px}}

